<template>
  <div class="addgood">
    <!-- 面包屑 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>权限列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert title="添加商品信息" type="info" center show-icon :closable="false"></el-alert>
      <el-steps :active="activeName-0" align-center>
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="商品完成"></el-step>
      </el-steps>
      <el-form
        :model="goodsAddForm"
        :rules="goodsRul"
        ref="goodsRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          v-model="activeName"
          @tab-click="handleClick"
          tab-position="left"
          :before-leave="handleBeforeLeave"
        >
          <el-tab-pane label="基本信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="goodsAddForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input type="number" v-model="goodsAddForm.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input type="number" v-model="goodsAddForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input type="number" v-model="goodsAddForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                v-model="goodsAddForm.goods_cat"
                :options="categoryList"
                :props="categoryProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <el-form-item v-for="item in manyData" :key="item.attr_id" :label="item.attr_name">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  v-for="(subitem,subindex) in item.attr_vals"
                  :key="subindex"
                  :label="subitem"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <el-form-item v-for="item in onlyData" :key="item.attr_id" :label="item.attr_name">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <el-upload
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :headers="uploadToken"
              :on-success="handleSuccess"
              list-type="picture"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <quill-editor v-model="goodsAddForm.goods_introduce" ref="myQuillEditor"></quill-editor>
            <el-button type="primary" @click="addGoods">提交</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import _ from "lodash";
export default {
  data() {
    return {
      activeName: "0",
      goodsdata: {},
       goodsAddForm: {
        goods_name: "",
        goods_price: "",
        goods_weight: "",
        goods_number: "",
        goods_cat: [],
        pics: [],
        goods_introduce: "",
        attrs: []
      },
      goodsRul: {
        goods_name: [
         { required: true, message: "请输入名称11", trigger: "blur" }
        ],
        goods_price: [
          {
            required: true,
            message: "请输入商品价格！",
            trigger: "blur"
          }
        ],
        goods_weight: [
          {
            required: true,
            message: "请输入商品重量！",
            trigger: "blur"
          }
        ],
        goods_number: [
          {
            required: true,
            message: "请输入商品数量！",
            trigger: "blur"
          }
        ],
        goods_cat: [
          {
            required: true,
            message: "请输入商品分类！",
            trigger: "blur"
          }
        ]
      },
     
      categoryList: [],
      categoryProps: {
        label: "cat_name",
        value: "cat_id",
        children: "children"
      },
      manyData: [],
      onlyData: [],
      uploadURL: "http://www.itsse.cn:8888/api/private/v1/upload",
      uploadToken: {
        Authorization: window.sessionStorage.getItem("token")
      },
      uploadImagePath: ""
    };
  },
  methods: {
    //   tab切换
    async handleClick() {
      if (this.activeName == 1) {
        let { data: res } = await this.$http.get(
          `categories/${this.categoryId}/attributes`,
          {
            params: {
              sel: "many"
            }
          }
        );
        if (res.meta.status != 200) {
          return this.$message.error("商品参数获取失败");
        }
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals.trim().split(" ");
        });
        this.manyData = res.data;
      } else if (this.activeName == 2) {
        let { data: res } = await this.$http.get(
          `categories/${this.categoryId}/attributes`,
          {
            params: {
              sel: "only"
            }
          }
        );
        if (res.meta.status != 200) {
          return this.$message.error("商品参数获取失败");
        }
        this.onlyData = res.data;
      }
    },
    // 级联列表 必须选择三个
    handleChange() {
      if (this.goodsAddForm.goods_cat.length != 3) {
        this.goodsAddForm.goods_cat = [];
      }
    },
    //获取级联列表
    async getCateList() {
      let { data: res } = await this.$http.get("categories");
      if (res.meta.status != 200) {
        return this.$message.error("商品分类获取失败");
      }
      this.categoryList = res.data;
    },
    // tab切页时的函数
    handleBeforeLeave(next, prev) {
      // console.log(next,prev);
      if (prev == 0 && this.categoryId == null) {
        this.$message.error("请必须选择到三级分类!!!");
        return false;
      }
    },
    handlePreview(file) {
      this.uploadURL = file.response.data.url;
    },
    handleRemove(file) {
      let temp_path = file.response.data.tmp_path;
      let index = this.goodsAddForm.pics.findIndex(
        item => item.pic == temp_path
      );
      this.goodsAddForm.pics.splice(index, 1);
      console.log(this.goodsAddForm.pics);
    },
    handleSuccess(resp) {
      if (resp.meta.status == 200) {
        this.goodsAddForm.pics.push({
          pic: resp.data.tmp_path
        });
      }
    },
     addGoods() {
      this.$refs.goodsRef.validate( async valid => {
        if (!valid) return this.$message.error("商品参数不能为空！！！");
        let from = _.cloneDeep(this.goodsAddForm);
        from.goods_cat = from.goods_cat.join(",");
        // 处理动态参数
        this.manyData.forEach(item => {
          from.attrs.push({
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(" ")
          });
        });
        // 处理静态参数
        this.onlyData.forEach(item => {
          from.attrs.push({
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          });
        });
        let {data:res} = await this.$http.post('goods',from)
        if(res.meta.status != 201){
          return this.$message.error('商品创建失败')
        }
        this.$message.success("商品创建成功")
        this.$router.push("/goods")
        
      });
    }
  },
  created: function() {
    this.getCateList();
  },
  computed: {
    categoryId: function() {
      if (this.goodsAddForm.goods_cat.length == 3) {
        return this.goodsAddForm.goods_cat[2];
      }

      return null;
    }
  }
};
</script>

<style>
.el-steps {
  margin: 15px 0;
}
.el-step__title {
  font-size: 12px;
}
.ql-container {
  height: 400px;
}
</style>
